












import Vue from "vue";
import { Component } from "vue-property-decorator";
import { ChatService } from "@/x-vue/services/chat/chat.service";
import { ApiService } from "@/x-vue/services/api.service";
import { AppService } from "@/service/app.service";
import { ChatRoomService } from "@/x-vue/services/chat/chat.room";

@Component({})
export default class extends Vue {
  app = AppService.instance;
  chat = ChatService.instance;
  idxOrEmail = "";

  mounted(): void {
    console.log("chat; ", this.chat);
  }
  async onBeginChat(): Promise<void> {
    console.log("idxOrEmail; ", this.idxOrEmail);
    try {
      const user = await ApiService.instance.otherUserProfile(this.idxOrEmail);
      console.log("user; ", user);
      ChatRoomService.instance.enter({ users: [user.firebaseUid] });
    } catch (e) {
      this.app.error(e);
    }
  }
}
